<template><div>
  
    <nav class="navbar navbar-expand-lg navbar-dark bg-white">
      <div class="container-fluid">
        <a class="navbar-brand text-dark" href="#">N I K E MALL</a>
        <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse " id="navbarSupportedContent">
          <ul class="navbar-nav me-auto mb-2 mb-lg-0 text-dark">
            <li class="nav-item ">
              <a class="nav-link text-dark" aria-current="page" href="#">홈</a>
            </li>
            <li class="nav-item">
              <a class="nav-link active text-dark" href="#">남성</a>
            </li>
            <li class="nav-item">
              <router-link to="/detail">
              <a class="nav-link text-dark" href="#">의류</a>
              </router-link>
            </li>
            <li class="nav-item">
              <a class="nav-link text-dark" href="#">식품</a>
            </li>
          </ul>
          
          <form class="d-flex">
            <input class="form-control me-2 border-top-0 border-start-0 border-end-0 rounded-0" type="search" placeholder="Search" aria-label="Search">
            <button class="btn btn-outline-success border-0 bg-white" type="submit"><img src="https://cdn-icons-png.flaticon.com/512/71/71403.png" width="15" height="15" alt=""></button>
          </form>
        </div>
      </div>
    </nav>
    <div style="margin: 20px 0">
    <p class="d-grid gap-2 d-md-flex justify-content-md-end bt-4">
          <button  @click="priceSort()" type="button" class="btn btn-outline-dark">금액순</button>
  <button @click="sortBack()" type="button" class="btn btn-outline-dark">최신순</button>
  </p></div>
  <Md @closeModal="모달창열렸니=false" :상품="상품" :누른거="누른거" :모달창열렸니="모달창열렸니"></Md>
    <Card @openModal="모달창열렸니=true; 누른거 = $event"   :상품="상품[i]" v-for="(a,i) in 상품" :key="i" ></Card>
    </div>
  
  </template>
  
  <script>
    import Card from './card.vue';
    import data from '../assets/product.js';
    import Md from './modal.vue';
  
    
    export default{
    components:{
      Card:Card,
      Md:Md,
    },
      data(){
        return{
          상품:data,
          상품오리지널:[...data],
          모달창열렸니:false,
          누른거:0,
        }
      },
  
      methods:{
        priceSort(){
        this.상품.sort(function(a,b){
          return a.price-b.price; 
        })
      },
      sortBack(){
        this.상품 = [... this.상품오리지널];
        
      }
      }
    }
  </script>
  
